.van-button {
  position: relative;
  padding: 0;
  display: inline-block;
  height: 45px;
  line-height: 43px;
  border-radius: 3px;
  box-sizing: border-box;
  font-size: 16px;
  text-align: center;
  -webkit-appearance: none
}

.van-button::before {
  content: " ";
  position: absolute;
  top: 50%;
  left: 50%;
  opacity: 0;
  width: 100%;
  height: 100%;
  border: inherit;
  border-color: #000;
  background-color: #000;
  border-radius: inherit;/*inherit parent's border radius
 */
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
}

.van-button:active::before {
  opacity: .3;
}

.van-button--unclickable::before {
    display: none;
}

.van-button--default {
    color: #333;
    background-color: #fff;
    border: 1px solid #eee;
}

.van-button--primary {
    color: #fff;
    background-color: #0074e4;
    border: 1px solid #0074e4;
}

.van-button--danger {
    color: #fff;
    background-color: #f44;
    border: 1px solid #f44;
}

.van-button--large {
    width: 100%;
    height: 50px;
    line-height: 48px;
}

.van-button--normal {
    padding: 0 15px;
    font-size: 14px;
}

.van-button--small {
    height: 30px;
    padding: 0 8px;
    min-width: 60px;
    font-size: 12px;
    line-height: 28px;
}

.van-button--loading .van-loading {
      display: inline-block;
    }

.van-button--loading .van-button__text {
      display: none;
    }

/*mini图标默认宽度50px，文字不能超过4个
 */

.van-button--mini {
    display: inline-block;
    width: 50px;
    height: 22px;
    line-height: 20px;
    font-size: 10px;
}

.van-button--mini + .van-button--mini {
  margin-left: 5px;
}

.van-button--block {
    width: 100%;
    display: block;
}

.van-button--bottom-action {
    width: 100%;
    height: 50px;
    line-height: 50px;
    border: 0;
    border-radius: 0;
    font-size: 16px;
    color: #fff;
    background-color: #f85;
}

.van-button--bottom-action.van-button--primary {
  background-color: #0074e4;
}

.van-button--disabled {
    color: #fff;
    background-color: #a1a1a1;
    border: 1px solid #a1a1a1;
}

.van-cell {
  width: 100%;
  display: -webkit-box;
  display: -webkit-flex;
  display: flex;
  padding: 10px 15px;
  box-sizing: border-box;
  line-height: 24px;
  position: relative;
  background-color: #fff;
  color: #333;
  font-size: 14px;
  overflow: hidden
}

.van-cell:not(:last-child)::after {
  left: 15px;
  right: 0;
  width: auto;
  -webkit-transform: scale(1, .5);
          transform: scale(1, .5);
  border-bottom-width: 1px;
}

.van-cell-group {
    background-color: #fff;
}

.van-cell__label {
    font-size: 12px;
    line-height: 1.2;
    color: #666;
}

.van-cell__title, .van-cell__value {
    -webkit-box-flex: 1;
    -webkit-flex: 1;
            flex: 1;
}

.van-cell__value {
    overflow: hidden;
    text-align: right;
    vertical-align: middle;
}

.van-cell__value--alone {
      text-align: left;
    }

.van-cell__left-icon {
    font-size: 16px;
    line-height: 24px;
    margin-right: 5px;
}

.van-cell__right-icon {
    color: #999;
    font-size: 12px;
    line-height: 24px;
    margin-left: 5px;
}

.van-cell--clickable:active {
  background-color: #e8e8e8;
}

.van-cell--required {
    overflow: visible;
}

.van-cell--required::before {
  content: '*';
  position: absolute;
  left: 7px;
  font-size: 14px;
  color: #f44;
}

.van-cell--center {
    -webkit-box-align: center;
    -webkit-align-items: center;
            align-items: center;
}

